body {
  font-family: $main-font;
  font-size: 12px;
  line-height: 1.33;
  color: $text-cl;
  background-color: $page-bg-cl;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
